import { InjectionToken } from '@angular/core';

/**
 * Config for setting up TransactionSigningModule
 */
export interface TransactionSigningConfig {
  /**
   * Whether the signing should follow a redirect or headless flow.
   * (Defaults to false)
   */
  useRedirectFlow: boolean;
  /**
   * Time between polling attempts in milliseconds.
   * (Defaults to 3000)
   */
  pollingCycleTime: number;
  /**
   * Whether resend push notifications are enabled.
   * (Defaults to false)
   */
  enableResendPushNotification: boolean;
}

/**
 * TransactionSigningConfig Injection Token
 */
export const TRANSACTION_SIGNING_CONFIG = new InjectionToken<TransactionSigningConfig>(
  'TransactionSigningConfig injection token',
);
